import React from "react";
import "./SpecialistsSection.css";

const specialists = [
  {
    name: "Dr. John Smith",
    role: "Lead Dentist",
    img: "/Dr. John Smith.jpg",
  },
  {
    name: "Dr. Emily Lee",
    role: "Orthodontist",
    img: "/Dr. Emily Lee.jpg",
  },
  {
    name: "Dr. Sarah Thompson",
    role: "Registered Hygienist",
    img: "/Dr. Sarah Thompson.jpg",
  },
  {
    name: "Dr. Askary",
    role: "Orthodontist",
    img: "/Dr. Askary.jpg",
  },
];

const SpecialistsSection = () => {
  return (
    <section className="specialists-section">
      <div className="specialists-header">
        <span className="tag"># SPECIALISTS</span>
        <h2>
          Meet our <br /> specialists
        </h2>
      </div>

      <div className="specialists-grid">
        {specialists.map((doc, index) => (
          <div className="specialist-card" key={index}>
            <div className="specialist-img">
              <img src={doc.img} alt={doc.name} />
            </div>
            <h3>{doc.name}</h3>
            <p>{doc.role}</p>
          </div>
        ))}
      </div>
    </section>
  );
};

export default SpecialistsSection;
